import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Result from './component/result';
import "./ResultPage.css";

import Ari from "./component/foto all MT/Ari Ratna Gumilang.jpeg";
import Astrid from "./component/foto all MT/Astrid.jpeg";
import Dita from "./component/foto all MT/Dita Khairunnisa.jpeg";
import Edi from "./component/foto all MT/Edi Kurniadi.jpeg";
import Elisa from "./component/foto all MT/Elisa.jpeg";
import Fahmi from "./component/foto all MT/Fahmi Murtadha.jpeg";
import Kiki from "./component/foto all MT/Felicia Kiki Simanjaya.jpeg";
import Novi from "./component/foto all MT/Felicia Novi Simanjaya.jpeg";
import Inda from "./component/foto all MT/Inda Purnama.jpeg";
import Indah from "./component/foto all MT/Indah Eka Asriani.jpeg";
import Sawir from "./component/foto all MT/Musawwir Usman.jpeg";
import Nadia from "./component/foto all MT/Nadia Andini.jpeg";
import Nunung from "./component/foto all MT/Nunung Kadarsih.jpeg";
import Nury from "./component/foto all MT/Nury Kamelia.jpeg";
import Oki from "./component/foto all MT/Oki Anggara.jpeg";
import Rifqah from "./component/foto all MT/Rifqah Sajidah.jpeg";
import Sari from "./component/foto all MT/Sari Setiawati.jpeg";
import Shafira from "./component/foto all MT/Shafira Kurnia.jpeg";

const nominatorsImg = [Ari, Astrid, Dita, Edi, Elisa, Fahmi, Kiki, Novi, Inda, Indah, Sawir, Nadia, Nunung, Nury, Oki, Rifqah, Sari, Shafira];
const nominatorsName = ['Ari', 'Astrid', 'Dita', 'Edi', 'Elisa', 'Fahmi', 'Kiki', 'Novi', 'Inda', 'Indah', 'Sawir', 'Nadia', 'Nunung', 'Nury', 'Oki', 'Rifqah', 'Sari', 'Shafira'];
const nominatorsFullName = ['Ari Ratna', 'Astrid', 'Dita Khairunnisa', 'Edi Kurniadi', 'Elisa', 'Fahmi Murtadha', 'Felicia Kiki', 'Felicia Novi', 'Inda Purnama', 'Indah Eka Asriani', 'Musawwir Usman', 'Nadia Andini', 'Nunung Kadarsih', 'Nury Kamelia', 'Oki Anggara', 'Rifqah Sajidah', 'Sari Setiawati', 'Shafira Kurnia'];

const ResultPage = () => {

  const [nominators, setNominators] = useState(null);
  const [stateNom, setStateNom] = useState(null);
  const [totalVoter, setTotalVoter] = useState(0);

  const idx = (name) => {
    return nominatorsName.indexOf(name);
  }



  useEffect(() => {
    axios.get(`https://form-app-server.cyclic.app/allNominators`)
      .then(res => {
       res.data.sort((a,b) => {
         return b.voter - a.voter
       });
       setNominators(res.data);
       console.log(res.data)

       let temp_totalVoter = 0;
       for(let i = 0 ; i < res.data.length ; i++) {
          temp_totalVoter += res.data[i].voter
       }
       setTotalVoter(temp_totalVoter)
      })
  }, [stateNom])

    if(!nominators) {
      return (<div className="component">loading data</div>)
    } else {
      console.log(totalVoter);
      return (
        <div className="component">
          <h2 className="result__title">Hasil Pemilihan<br/> MT of The Month</h2>
          {nominators.map((nominator, index) => {
            return <Result name={nominatorsFullName[idx(nominators[index].name)]} src_img={nominatorsImg[idx(nominators[index].name)]} voter={nominators[index].voter} percentage={ Math.round(nominators[index].voter/totalVoter*300) } />
          })}
        </div>
     );
    }
}
  
export default ResultPage;

